export default Landing;

const Container = styled.div`
  width: 100vw;
  height: 100vh;
  background-color: #151515;
  font-family: "Quicksand", sans-serif;
  color: white;
  z-index:10;
    /* screen width is less than 450px (small) */
  ${media.lessThan("small")`
    .scroll-arrow{
 display:none;
    }
  `}
    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
    .scroll-arrow{
 display:none;
    }
   
  `}
    /* screen width is greater than 1170px (large) */
  ${media.greaterThan("large")`
    height: 120vh;
  `}
`;

const HeroBanner = styled.div`
  width: 100vw;
  height: 424px;
  position: relative;
  overflow: hidden;
  .banner-content {
    background: rgba(67, 67, 67, 0.44);
    height: 60%;
    transform: translate3d(0, 35%, 0);
  }
  h3 {
    transform: translate3d(0, 20%, 0);
    width: 50%;
    margin: 0 auto;
    font-family: "Quicksand";
    font-style: normal;
    font-weight: normal;
    font-size: 100px;
    line-height: 105px;
    text-align: center;
    letter-spacing: 0.725em;
    text-transform: uppercase;
  }
  /* screen width is less than 450px (small) */
  ${media.lessThan("small")`
    height: 224px;
    .banner-content{
      height: 60%;
      transform: translate3d(0, 15%, 0);
    }
    h3{
    font-size: 32px;
    line-height: 50px;
      }
  `}
    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
  height: 324px;
  .banner-content{
    height: 30%;
      transform: translate3d(0, 45%, 0);
    }
h3{
  font-size: 22px;
    line-height: 50px;
}
  `}

    /* screen width is between 768px (medium) and 1170px (large) */
  ${media.between("medium", "large")`
 
    h3{
    font-size: 60px;
    line-height: 100px;
}
  `}
  ${media.greaterThan("large")`
    /* screen width is greater than 1170px (large) */
    h3{
    font-size: 80px;
    line-height: 100px;
}
  `}

`;

const HeroDiv = styled.div`
  width: 100vw;
  height: 424px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("/static/images/landing/hero-normal.png") center center
    no-repeat;
  background-size: cover;
  transition: all 0.2s ease-in-out;

  &:hover {
    transform: scale3d(1.05, 1.05, 1.05);
  }
  ${media.lessThan("small")`
    /* screen width is less than 450px (small) */  
    height: 184px;
    background-size: cover;
  `}

    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
    height: 204px;
    background-size: cover;
  `}
`;

const HeroContent = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 30vh;
  .hero-content {
    width: 100%;
  }
  .hero-p-b{
    p{
      animation: heropanimation 1s forwards 0s ease-in-out;      
    }
    @keyframes heropanimation{
    0%{
        transform: translateY(80%);
        opacity: 0;
        }
    
    100%{
        transform:  translateY(0%);
        opacity: 1;
        }
}
  }
  p {
    font-family: "Quicksand";
    font-style: normal;
    font-weight: normal;
    font-size: 32px;
    line-height: 42px;
    letter-spacing: 0.27em;
    color: #eeeeee;
    width: 30%;
    padding-left: 10%;
  }
  ${media.lessThan("small")`
    /* screen width is less than 450px (small) */
    height: 10vh;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-between;

    .hero-p-b{
       display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-between;
    
    
    
    }


p{
  font-size: 18px;
  line-height: 42px;
  text-align: right;
  margin-left: 30vw;
}

  `}
    /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
    height: 10vh;
    display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-between;

    .hero-p-b{
       display: flex;
    flex-direction: column;
    align-items:center;
    justify-content: space-around;
    }
p{
  font-size: 18px;
  line-height: 32px;
  text-align: right;
  /* margin-left: 30vw; */
}
  `}
`;
const ButtonContainer = styled.button`
  /* clip-path: polygon(15% 0%, 100% 0%, 75% 100%, 0% 100%); */
  /* width: 20%; */
  height: 60px;
  border: none;
  background-color: #151515;
  text-align: center;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  overflow: visible;
  outline: none;
  &:hover {
    transform: scale3d(1.1, 1.1, 1.1);
    svg {
      .line1 {
        transform: translateX(0.25vw);
      }
      .line2 {
        transform: translateX(0.55vw);
      }
      .line3 {
        transform: translateX(0.95vw);
      }
      .line4 {
        transform: translateX(-0.65vw);
      }
    }
  }

  /* screen width is between 450px and 768px (small to medium) */
  ${media.between("small", "medium")`
    height: 50px;
  `}
`;

const ScrollDiv = styled.div`
  animation: scrollanimation 5s infinite 0s ease-in-out;

  @keyframes scrollanimation {
    0% {
      transform: translateY(80%);
      opacity: 0;
    }

    100% {
      transform: translateY(100%);
      opacity: 1;
    }
  }

  ${media.lessThan("small")`
    /* screen width is less than 450px (small) */

display:none;
  `}

  ${media.between("small", "medium")`
  display:none;
  `}
`;
